<template>
  <div id="container">
    <swiper :options="swiperOption" ref="mySwiper">
      <swiper-slide class="swiper-slide games">
        <img src="../assets/img/1482371383446.png" alt />
      </swiper-slide>
      <swiper-slide class="swiper-slide games">
        <img src="../assets/img/1558601736686.jpg" alt />
      </swiper-slide>
      <swiper-slide class="swiper-slide games">
        <img src="../assets/img/1558601750513.png" alt />
      </swiper-slide>
      <div class="swiper-pagination" slot="pagination"></div>      
    </swiper>
  </div>
</template>
<script>
import { swiper, swiperSlide } from "vue-awesome-swiper";
export default {
  name: "carrousel",
  data() {
    return {
      swiperOption: {
        autoplay: {
          disableOnInteraction: false, //
          delay: 3000 //
        }, //
        speed: 2000, //sliderms
        loop: true, //
        grabCursor: true, //trueSwiper
        // setWrapperSize: true, //Swiperflexbox(display: flex)Wrapperslidesflexbox
        autoHeight: true, //truewrappercontainerslide
        scrollbar: ".swiper-scrollbar",
        mousewheelControl: true, //trueslide
        observeParents: true, //swiper/swiperswiper

        pagination: {
          el: ".swiper-pagination",
          // type : 'progressbar', //
          clickable: true //Swiper
        },
        navigation: {
          nextEl: ".swiper-button-next",
          prevEl: ".swiper-button-prev",          
        }
      },
      computed: {
        swiper() {
          return this.$refs.mySwiper.swiper;
        }
      }
    };
  }
};
</script>
<style scoped>
img {
  width: 100%;
  height: auto;
}
</style>